import NiceModal, { useModal, antdModal } from "@ebay/nice-modal-react";
import { Form, Input, Modal } from "antd";
import { useCallback } from "react";
import { IMeeting } from "./types";

export default NiceModal.create((
  {meeting}: {meeting: IMeeting}
  ) => {
  const modal = useModal();
  const [form] = Form.useForm();

  const handleSubmit = useCallback(() => {
    form.validateFields().then(() => {
      const newCompany = { ...form.getFieldsValue() };
      modal.resolve(newCompany);
      modal.hide();
    });
  }, [
    modal,
    // user,
    form,
  ]);
  return (
    <Modal
      {...antdModal(modal)}
      title="–°–æ–∑–¥–∞—Ç—å —Å–æ–±—Ä–∞–Ω–∏–µ"
      //   okText={user ? "Update" : "Create"}
      okText="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
      cancelText="–û—Ç–º–µ–Ω–∞"
      onOk={handleSubmit}    
    >
      <Form form={form} layout='vertical'>
        <Form.Item
          name="name"
          label="–£—Å–ª–æ–≤–Ω–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ —Å–æ–±—Ä–∞–Ω–∏—è"
          rules={[
            {
              required: true,
              message: "–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –¥–∞–Ω–Ω–æ–µ –ø–æ–ª–µ",
            },
          ]}
        >
          <Input />
        </Form.Item>        
      </Form>
    </Modal>
  );
});